@import '../untils/variables';

.form {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  padding: 20px 10px 10px;
  width: 650px;
  overflow: auto;
  background: $form-background-color;


  &--loading {
    &::before {
      content: '';
      position: absolute;
      z-index: 100;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: $form-background-color;
      opacity: 0.9;
    }
  }

  &__row {
    display: flex;
    margin-bottom: 10px;
  }

  &__fieldset {
    flex-shrink: 0;
    padding: 0 10px;

    &--full {
      width: 100%;
    }

    &--half {
      width: 50%;
    }
  }

  &__footer {
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: auto 0 0;
    padding: 10px 0;
  }
}
