@import '../untils/variables';

.table {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  border-collapse: collapse;
  text-align: center;
  overflow: hidden;

  &__row {
    width: 100vh;
  }

  &__cell {
    padding: 5px;
    text-align: center;
    width: 45px;
    height: 20px;
    border: 1px solid $table-border-color;

    &--time {
      color: $table-time-color;
      background: $table-time-background-color;
      border-color: $table-time-border-color;
    }

    &--date {
      max-width: 180px;
      min-width: 180px;
      font-weight: 500;
      text-align: start;
      font-size: 17px;
      background: $table-date-background-color;
    }

    &--staff {
      max-width: 180px;
      min-width: 180px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      text-align: start;

      &:hover {
        overflow: visible;
      }

    }
  }
}




