@import '../untils/variables';

.recording-header {
  &__item {


    #{$recording--loading} & {
      visibility: hidden;
    }




    &--datа {
      display: flex;
      justify-content: center;
      flex-direction: column;
      gap: 5px;
      align-items: center;
      padding: 10px;

      #{$recording--loading} & {
        visibility: hidden;
      }
    }



    &-info {
      position: relative;
      padding: 10px;
      width: 650px;
      background: $recording-lid-background-color;
      border: solid $form-border-color;
      border-width: 1px 0 1px 1px;
      box-shadow: 0 0 30px $form-box-shadow-color;
      border-radius:  10px;



      position: absolute;
      top: 90px;
      left: 0;
      width: 650px;
      z-index: 2;

      &-show {
        display: flex;

      }
    }
  }
}

