@import '../untils/variables';

.recording-section {
  width: 650px;
  align-self: center;
  background: $recording-lid-background-color;
  border: solid $form-border-color;
  border-width: 1px 0 1px 1px;
  box-shadow: 0 0 30px $form-box-shadow-color;
  border-radius: 10px;

  &__header {
    position: relative;
    display: flex;
    align-items: center;
    padding: 10px;
  }

  &__title {
    flex-grow: 2;
    padding: 10px 15px;
    font-size: 17px;
    font-weight: 700;
    line-height: 18px;
  }

  &__btn {
    margin: 0 5px;
  }

  &__info {
    position: absolute;
    display: none;

    &--show {
      display: flex;
    }

  }
}
