@import '../untils/variables';

.recording {
  min-height: 100vh;
  padding: 16px;
  background: $recording-bg;

  &--loading {
    $recording--loading: & !global;
  }

  &__header {
    display: flex;
    flex-direction: inherit;
    justify-content: space-between;





    &--pre {
      position: relative;
      margin: 0 0 0 auto;
      padding: 10px;
      width: 650px;
      background: $recording-pre-background-color;
      border: solid $form-border-color;
      border-width: 1px 0 1px 1px;
      box-shadow: 0 0 30px $form-box-shadow-color;
      border-radius:  10px;

      #{$recording--loading} & {
        visibility: hidden;
      }

      &-info {
        position: absolute;
        top: 90px;
        left: 0;
        width: 650px;
        z-index: 2;

        &--show {
          display: flex;
        }
      }

    }
  }

  &__table {
    grid-column: 1/4;
    grid-row: 2/3;
    margin: 0;
    padding: 0;

    #{$recording--loading} & {
      visibility: hidden;
    }
  }

  &__text {
    padding: 10px 15px;
    font-size: 17px;
    font-weight: 700;
    line-height: 18px;
  }

  &__loader {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none;

    #{$recording--loading} & {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    span {
      width: 30px;
      height: 30px;
      border-radius: 100%;
      background-color: $recording-loader-color;
      margin: 35px 5px;
      opacity: 0;

      &:nth-child(1) {
        animation: opacityChange 1s ease-in-out infinite;
      }

      &:nth-child(2) {
        animation: opacityChange 1s ease-in-out 0.33s infinite;
      }

      &:nth-child(3) {
        animation: opacityChange 1s ease-in-out 0.66s infinite;
      }
    }
  }
}

@keyframes opacityChange {
  0%,
  100% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
}
